import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { User, ChevronDown, ChevronRight, Package, LayoutGrid } from 'lucide-react';

interface Node {
    id: string;
    name: string;
    rank: string;
    left?: Node;
    right?: Node;
}

const RankBadge = ({ rank }: { rank: string }) => {
    const rankColors: Record<string, string> = {
        'Associate': '#A0A0A0',
        'Senior Associate': '#2997ff',
        'Manager': '#30d158',
        'Senior Manager': '#ffd60a',
        'Director': '#ff375f',
        'Senior Director': '#bf5af2',
        'Executive Director': '#ffffff'
    };

    return (
        <span className="rank-badge" style={{ borderColor: rankColors[rank], color: rankColors[rank] }}>
            {rank}
        </span>
    );
};

const TreeNode = ({ node }: { node: Node }) => {
    const [isOpen, setIsOpen] = useState(true);

    return (
        <div className="tree-node-container">
            <div className="tree-node glass-panel">
                <div className="node-avatar">
                    <User size={20} />
                </div>
                <div className="node-info">
                    <span className="node-name">{node.name}</span>
                    <RankBadge rank={node.rank} />
                </div>
                {(node.left || node.right) && (
                    <button className="toggle-btn" onClick={() => setIsOpen(!isOpen)}>
                        {isOpen ? <ChevronDown size={14} /> : <ChevronRight size={14} />}
                    </button>
                )}
            </div>

            <AnimatePresence>
                {isOpen && (node.left || node.right) && (
                    <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        className="node-children"
                    >
                        <div className="branch left">
                            {node.left ? <TreeNode node={node.left} /> : <div className="empty-slot glass-panel">LEFT LEG EMPTY</div>}
                        </div>
                        <div className="branch right">
                            {node.right ? <TreeNode node={node.right} /> : <div className="empty-slot glass-panel">RIGHT LEG EMPTY</div>}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
};

export const GenealogyTree: React.FC = () => {
    // Mock complex tree
    const rootNode: Node = {
        id: '1',
        name: 'Slade Johnson',
        rank: 'Executive Director',
        left: {
            id: '2',
            name: 'Alex Rivera',
            rank: 'Director',
            left: { id: '4', name: 'James Wilson', rank: 'Manager' },
            right: { id: '5', name: 'Sarah Chen', rank: 'Associate' }
        },
        right: {
            id: '3',
            name: 'Elena Vance',
            rank: 'Senior Director',
            left: { id: '6', name: 'Marcus Aurelius', rank: 'Senior Manager' }
        }
    };

    return (
        <div className="genealogy-view">
            <div className="tree-header">
                <h3>Binary Genealogy</h3>
                <p>Drag from holding tank to place new partners.</p>
            </div>
            <div className="tree-scroll-container">
                <TreeNode node={rootNode} />
            </div>
        </div>
    );
};

export const HoldingTank: React.FC = () => {
    const unplaced = [
        { id: '101', name: 'David Smith', date: '2h ago' },
        { id: '102', name: 'Luna Love', date: '5h ago' },
        { id: '103', name: 'Crypto King', date: '1d ago' }
    ];

    return (
        <div className="holding-tank glass-panel">
            <div className="tank-header">
                <Package size={18} />
                <span>Holding Tank</span>
                <span className="count">{unplaced.length}</span>
            </div>
            <div className="tank-list">
                {unplaced.map(user => (
                    <div key={user.id} className="tank-item glass-card" draggable>
                        <LayoutGrid size={14} className="drag-handle" />
                        <div className="user-meta">
                            <span className="name">{user.name}</span>
                            <span className="time">{user.date}</span>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};
